.navContainer{
    height: 8.264vw;
    padding-right: 7.639vw;
    padding-left: 4.737vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.menu{
    gap: 2.292vw;
}
.logo{
    gap:1.139vw;
    display: flex;
    align-items: center;
    justify-content: center;
}
.logo img{
    margin-top: 0.825vw;
    height: 6.158vw;
    width: 6.158vw;
}
.navDropDown{
    position: absolute;
    top: 6.944vw;
    /* right: 540px; */
    width: 100vw;
    height: 100%;
    /* left: 158px; */
    background-color: transparent;
    /* width: 250px; */
    /* color: black; */
    /* padding: 3.354vw 8.472vw 2.061vw 8.472vw; */
    z-index: 1;
}
.navLink{
    display: flex;
    justify-content: space-evenly;
    color: white;
    gap: 2.292vw;

}
.dropdown{
    text-decoration: none;
    color: black;
    margin: 1.389vw 0.694vw;
    cursor: pointer;
}
.subLink p{
    margin-top: 16px;
}
.hamburger-menu{
    display: none;
    position: absolute;
    top: 1.833vw;
    right: 1.556vw;
    flex-direction: column;
    justify-content: space-between;
    width: 2.5vw;
    height: 2.222vw;
}
.hamburger-menu span{
    height: 0.444vw;
    width: 100%;
    background-color: white;
    border-radius: 0.222vw;
    cursor: pointer;
}
/* .open{
    display: flex;
    justify-content: space-evenly;
    color: white;
    text-align: center;
        flex-direction: column;
    gap: 2.292vw;
} */
.contactButton{
    display: block;
    color: black;
    background-color: white;
    padding: 1.25vw 1.667vw ;
    border-radius: 2.778vw;
    font-family: DM Sans;
    font-size: 1.25vw;
    font-weight: bold;
    text-align: center;
}
.button{
    color: black;
    background-color: white;
    padding: 1.25vw 1.667vw ;
    border-radius: 2.778vw;
    font-family: DM Sans;
    font-size: 1.25vw;
    font-weight: bold;
}
.dropdownData{
    display: flex;
}
@media (max-width:700px ) {
    
    .logo{
        justify-content: flex-start;
    }
    .contactButton{
        display: none;
    }
    .navLink{
        display: none;
        text-align: center;
        flex-direction: column;
    }
    .navContainer{
        display: block;
        height: 100%;
    }
    .hamburger-menu{
        display: flex;
    }
    /* .button{
        display: none;
    } */
    
    div.open{
        display: block;
    }
    p{
        padding: 0.347vw 0;
    }
  
    .navDropDown{
        top: 20.944vw;
    }
}